import Vue from 'vue';
import VueRouter from 'vue-router';
import HomeView from '@/views/HomeView';
import JobsListView from '@/views/JobsListView';
import Found404View from '@/views/404View';
import JobInfoView from '@/views/JobInfoView';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: HomeView,
  },
  {
    path: '/jobs/:langues/:search',
    name: 'jobsPage',
    component: JobsListView,
    children: [
      {
        path: '/jobs/:langues/:search/viewjob/:id',
        name: 'FullInfo',
        component: () => import('@/components/JobsInfoComponent'),
      },
    ],
  },
  {
    path: '/fullInfoJob/:id',
    name: 'JobInfoView',
    component: JobInfoView,
  },
  {
    path: '/*',
    name: '404 not found',
    component: Found404View,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
