<template>
  <div class="company-wrapper">
    <div
      class="company-wrapper-bigItem"
      v-for="item in listJobs"
      :key="item.id"
    >
      <h1>
        <router-link :to="'jobs/' + item.name + '/All'"
          >{{ item.name }}<span>{{ item.count }}</span></router-link
        >
      </h1>
      <router-link
        :to="'/jobs/' + 'All' + '/All' + '/viewjob/' + item.id"
        v-for="itemLink in item.vacancies"
        :key="itemLink.link"
        class="underline-btn"
        >{{ itemLink.title }}
        <img width="25px" :src="itemLink.company_logo" alt="" />{{
          itemLink.company_name
        }}
      </router-link>
    </div>
  </div>
</template>

<script>
import Api from '@/api/Api';

export default {
  name: 'CompanyComonent',
  data() {
    return {
      listJobs: [],
    };
  },
  methods: {
    async getVacanciesData() {
      const response = await Api.getHomeVacanciesData();
      this.listJobs = response.data.map((item) => {
        const { id, name, vacancies_count, vacancies } = item;
        return {
          id,
          name,
          count: vacancies_count,
          vacancies,
        };
      });
    },
  },
  mounted() {
    this.getVacanciesData();
  },
};
</script>

<style src="./ComponyComponent.scss" lang="scss"></style>
