<template>
  <div class="container">
    <div class="search-wrapper">
      <span class="icon icon-search"></span>
      <v-select
        v-model="e7"
        :items="states"
        multiple
        small-chips
        flat
        solo
        label="Texnologylar boyicha qidrish"
        color="#0062FF"
        class="search-select"
        ><template #selection="{ item }">
          <v-chip
            outlined
            color="primary"
            close
            @click:close="deleteChip(item, e7)"
            >{{ item.text }}</v-chip
          >
        </template></v-select
      >
      <input
        v-model="searchInput"
        type="text"
        placeholder="Sarlavha Boyicha qidirish"
        class="inputSearch"
      />
      <button @click="searchjobRouter" class="search-wrapper-btn">
        Search
      </button>
    </div>
    <v-divider></v-divider>
  </div>
</template>

<script>
import Api from '@/api/Api';

export default {
  name: 'SearchComponent',
  data() {
    return {
      e7: [],
      states: [],
      searchInput: this.$route.params.search,
    };
  },
  methods: {
    deleteChip(itemNeedToRemove, array) {
      for (let i = 0; i < array.length; i += 1) {
        if (array[parseInt(i, 10)] === itemNeedToRemove) {
          array.splice(i, 1);
        }
      }
    },
    searchjobRouter() {
      let langues = [this.e7];
      this.$router.push({
        path: `/jobs/${langues}/${this.searchInput ? this.searchInput : ' '}`,
      });
      this.$emit('changeParam');
    },
    async changesSearchValue() {
      const response = await Api.getTexnologyData();
      this.states = response.data.map((item) => {
        const { name: text } = item;
        return {
          text,
        };
      });
      if (this.$route.params.langues) {
        this.e7 = this.$route.params.langues.split(',');
      }
    },
  },
  mounted() {
    this.changesSearchValue();
  },
};
</script>

<style src="./SearchComponent.scss" lang="scss"></style>
