<template>
  <div>
    <div>
      <HeaderComponent />
      <SearchComponent @changeParam="changeParam" />
    </div>
    <div class="container">
      <div class="jobs-wrapper">
        <div class="jobs-wrapper-left">
          <v-expansion-panels v-model="panel" multiple>
            <v-expansion-panel v-for="(item, i) in sortData" :key="i">
              <v-expansion-panel-header>{{
                item.title
              }}</v-expansion-panel-header>
              <v-expansion-panel-content
                v-for="items in item.items"
                :key="items.title"
                class="jobs-wrapper-left-checkboxMenu"
              >
                <div>
                  <input
                    @click="searchMenus(items.value)"
                    :name="item.value"
                    :id="items.value"
                    type="radio"
                  />
                  <label :for="items.value">{{ items.title }}</label>
                </div>
                <span>{{ items.count }}</span>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </div>
        <div class="jobs-wrapper-main">
          <ul>
            <li
              v-for="item in JobsData"
              :key="item.id + Math.random()"
              :class="[
                item.id == $route.params.id ? 'active-list' : '',
                'jobs-wrapper-main-items',
              ]"
            >
              <router-link
                :to="
                  '/jobs/' +
                  $route.params.langues +
                  '/' +
                  $route.params.search +
                  '/viewjob/' +
                  item.id
                "
                ><div class="jobs-wrapper-main-items-head">
                  <img
                    v-if="item.image"
                    width="50px"
                    height="50px"
                    :src="item.image"
                    alt="company logo"
                  />
                  <img
                    v-else
                    width="50px"
                    src="https://stories.td.com/volumes/default/article/221.gif"
                    alt="company logo"
                  />
                  <div>
                    <h3>
                      <router-link :to="'/fullInfoJob/' + item.id">{{
                        item.title
                      }}</router-link>
                    </h3>
                    <p>${{ item.salary }}</p>
                  </div>
                  <div class="jobs-wrapper-main-items-body-date">
                    <p>{{ item.date }}</p>
                  </div>
                </div>
                <div class="jobs-wrapper-main-items-body">
                  <div>
                    <div class="jobs-wrapper-main-items-body-type">
                      {{ item.job_type }}
                    </div>
                    <div class="jobs-wrapper-main-items-body-lavel">
                      {{ item.level ? item.level : 'Junior' }}
                    </div>
                  </div>
                </div></router-link
              >
            </li>
          </ul>
        </div>
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderComponent from '@/components/HeaderComponent';
import SearchComponent from '@/components/SearchCompoent';
import Api from '@/api/Api';
export default {
  name: 'JobsListView',
  components: {
    HeaderComponent,
    SearchComponent,
  },
  data() {
    return {
      panel: [0, 1],
      disabled: false,
      readonly: false,
      sortData: [
        {
          title: 'Ish vaqtlari boyicha',
          value: 'Time',
          items: [
            {
              title: 'Toliq kun',
              count: 82,
              value: 'time-full_time',
            },
            {
              title: 'Yarim kun',
              count: 99,
              value: 'time-part_time',
            },
            {
              title: 'soatlik ish',
              count: 12,
              value: 'time-intern',
            },
            {
              title: 'Masofaviy ish',
              count: 84,
              value: 'time-remote',
            },
          ],
        },
        {
          title: 'Ish Darajasi Boyicha',
          value: 'lavel',
          items: [
            {
              title: 'Junior',
              count: 95,
              value: 'lavel-junior',
            },
            {
              title: 'Strong junior',
              count: 63,
              value: 'lavel-sjunior',
            },
            {
              title: 'Middle',
              count: 35,
            },
            {
              title: 'Senior',
              count: 86,
              value: 'lavel-senior',
            },
            {
              title: 'Vip',
              count: 92,
              value: 'lavel-vip',
            },
          ],
        },
        {
          title: 'Ish Haqqi boyicha',
          value: 'salary',
          items: [
            {
              title: '$150 - $300',
              count: 95,
              value: 'salary-150-300',
            },
            {
              title: '$300 - $500',
              count: 63,
              value: 'salary-300-500',
            },
            {
              title: '$500 - $1000',
              count: 35,
              value: 'salary-500-1000',
            },
            {
              title: '$1000 - $1500',
              count: 86,
              value: 'salary-1000-1500',
            },
            {
              title: '$1500 - $2000',
              count: 92,
              value: 'salary-1500-2000',
            },
          ],
        },
      ],
      JobsData: [],
    };
  },
  methods: {
    async getJobsData(timeSort, levelsort, minSalary, maxSalary) {
      const Search =
        this.$route.params.search == 'All'
          ? undefined
          : this.$route.params.search;

      //
      const texnology =
        this.$route.params.langues == 'All'
          ? undefined
          : this.$route.params.langues == 'All';

      //

      const response = await Api.getVacanciesData({
        search: Search,
        technologies__name: texnology,
        min_salary: minSalary,
        max_salary: maxSalary,
        job_type: timeSort,
      });
      this.JobsData = response.data.map((item) => {
        const {
          company,
          experience,
          salary,
          created_at: date,
          image,
          id,
          job_type,
          title,
          level,
        } = item;
        return {
          company,
          experience,
          image,
          id,
          job_type,
          title,
          salary,
          date,
          level,
        };
      });
    },
    changeParam() {
      this.getSearchData();
    },
    searchMenus(value) {
      const sortBy = value.split('-');
      if (sortBy[0] == 'time') {
        this.getJobsData(sortBy[1]);
      } else if (sortBy[0] == 'lavel') {
        this.getJobsData(undefined, sortBy[1]);
      } else if (sortBy[0] == 'salary') {
        this.getJobsData(undefined, undefined, sortBy[1], sortBy[2]);
      }
    },
    async getSearchData() {
      if (
        this.$route.params.search == 'All' ||
        this.$route.params.search == ' ' ||
        this.$route.params.search == null ||
        this.$route.params.search == undefined
      ) {
        //
      } else {
        this.getJobsData();
      }
    },
  },
  mounted() {
    this.getJobsData();
  },
};
</script>

<style src="./JobsListView.scss" lang="scss"></style>
