<template>
  <div class="logos-wrapper">
    <h2>Вакансії провідних компаній</h2>
    <div class="logos-wrapper-items">
      <a v-for="item in logos" :href="item.website" :key="item.id">
        <img
          :src="'https://1kadr-api.pixyz.uz/' + item.img"
          alt="Componay logo"
        />
        <div class="countjob">
          {{ item.vacancies_count }}
        </div>
      </a>
    </div>
  </div>
</template>

<script>
import Api from '@/api/Api';

export default {
  name: 'ComponyLogosComponent',
  data() {
    return {
      logos: [
        {
          img: 'https://image.shutterstock.com/image-vector/abstract-circle-logo-260nw-642784204.jpg',
          vacancies_count: 52,
        },
        {
          img: 'https://cdn.logojoy.com/wp-content/uploads/2018/05/01104813/1268-768x591.png',
          vacancies_count: 52,
        },
      ],
    };
  },
  methods: {
    async getCompanyData() {
      const response = await Api.getHomeCompanyData();
      this.logos = response.data.map((item) => {
        const { id, logo: img, website, vacancies_count } = item;
        return {
          id,
          img,
          website,
          vacancies_count,
        };
      });
    },
  },
  mounted() {
    this.getCompanyData();
  },
};
</script>

<style src="./ComponyLogoComponent.scss" lang="scss"></style>
