import axios from 'axios';
import { baseUrl } from '@/config/';

axios.defaults.baseURL = baseUrl;

axios.interceptors.request.use((config) => {
  const token = localStorage.getItem('access_token');
  if (token && !config.url.startsWith('user-auth/')) {
    // eslint-disable-next-line no-param-reassign
    config.headers.Authorization = `Bearer ${token}`;
  }
  return config;
});

axios.interceptors.response.use(
  (response) => response,
  async (error) => Promise.reject(error)
);

export default {
  async request({ url, data, params, method }) {
    try {
      const body = {
        method,
        url,
        data,
        params,
      };
      // eslint-disable-next-line consistent-return
      const response = await axios(body).catch(async (error) => {
        if (error) {
          return error;
        }
      });
      return response;
    } catch (e) {
      this.emitMessage({
        detail: e.toString(),
        type: 'error',
      });
      return {
        error: {
          detail: e.toString(),
        },
      };
    }
  },
  get(url, params) {
    return this.request({
      method: 'GET',
      url,
      params,
    });
  },
  post(url, data) {
    const response = this.request({
      method: 'post',
      url,
      data,
    });

    return response;
  },
  getHomeCompanyData(params) {
    return this.get('/companies/home/', params);
  },
  getJobsData(params) {
    return this.get('/employees/', params);
  },
  getHomeVacanciesData(params) {
    return this.get('/vacancies/home/', params);
  },
  getVacanciesData(params) {
    return this.get('/vacancies/', params);
  },
  getTexnologyData(params) {
    return this.get('/common/skills/', params);
  },
  getVacancieData(id, params) {
    return this.get(`/vacancies/${id}`, params);
  },
};
